*{
    touch-action: none;
}
.gua-container{
    width: 100vw;
    height: 100vh;

    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    

    background-image: url('../../../public/images/bg_2.png');
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}
.gua-state-info{
    position: absolute;
    top: 5%;
    right: 5%;
    padding: 10px;
    font-size: 1vw;
    border: 3px solid #000;
}
.gua-broadcast {
    width: 50%;
    height: 20%;
    position: relative;
    
}
.gua-broadcast::before{
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    left: -60px;
    top: calc(50% - 20px);

    background-image: url('../../../public/images/arrow.png');
    background-repeat: no-repeat;
    background-size: cover;
    animation: shift 2s infinite;
}
@keyframes shift {
    0% {
        left: -60px;
    }
    50% {
        left: -80px;
    }
    100% {
        left: -60px;
    }
}
